@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
p,h2,h3,ul,body{margin:0;padding:0}html{font-family:"Roboto", sans-serif}.App__container{max-width:800px;margin:0 auto}.App__title{text-align:center}

.comments{display:flex;flex-direction:column;grid-row-gap:36px;row-gap:36px;list-style:none;border-radius:24px}.comments__item{padding:24px;border-radius:24px;box-shadow:2px 2px 8px #ccc;background-color:#fff}.comments__text{margin-top:24px;line-height:130%}

.user-info{display:flex;flex-wrap:wrap;grid-gap:24px;gap:24px;align-items:center;line-height:150%;font-weight:500}.user-info__picture{--profilePictureSize: 100px;display:flex;justify-content:center;align-items:center;flex-shrink:0;width:var(--profilePictureSize);height:var(--profilePictureSize);background-color:#639;border-radius:50%}.user-info__picture::before{content:"?";position:absolute;font-size:36px;color:#fff}.user-info__title{text-transform:capitalize}

.post-info__title{text-transform:capitalize;margin:16px 0}.post-info__main{margin-bottom:48px}

.post-list{display:flex;flex-direction:column;grid-row-gap:48px;row-gap:48px}.post-list__item{padding:36px;margin:0 24px;background-color:#eee;border-radius:24px}

